import QQMapWX from "@/common/js/qqmap-wx-jssdk.js"
import bannerSwiper from '@/components/mycom/swiper.vue';
import comList from '@/components/mycom/list.vue';
import navTitle from '@/components/mycom/nav.vue';
import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		bannerSwiper,
		comList,
		navTitle,
		uniSearchBar,
		uniLoadMore
	},
	data() {
		return {
			navList: [{
					type: 0,
					value: '有奖推荐'
				},
				{
					type: 1,
					value: '附近职位'
				},
				{
					type: 2,
					value: '最新职位'
				}
			],
			point: {
				latitude: '',
				longitude: ''
			},
			keyWords: '',
			bannerList: [],
			dataList: [],
			type: 0,
			page: 1,
			totalPage: 1,
			status: 'loading',
			city: ''
		};
	},
	onLoad() {
		this.loadData();
		this.getBanner();
		this.getPosition();

	},
	onReachBottom() {
		this.status = 'loading';
		this.totalPage > this.page ?
			((this.page += 1), this.loadData()) :
			setTimeout(() => {
				this.status = 'noMore';
			}, 30);
	},
	methods: {
		focus() {
			uni.hideTabBar();
		},
		blur() {
			uni.showTabBar();
		},
		getPosition() {
			if(uni.getStorageSync('city')){
				this.city=uni.getStorageSync('city')
				this.point=JSON.parse(uni.getStorageSync('point')) ;
				console.log(this.point.latitude)
			}else{
			let qqmapsdk = new QQMapWX({
				key: 'GQZBZ-3CECD-BP443-PBQEY-4R3M6-QMFBO'
			});
			let self = this;
			uni.getLocation({
				type: "gcj02",
				success(res) {
					console.log(res);
					self.point=res;
					uni.setStorageSync('point',res);
					qqmapsdk.reverseGeocoder({
						location: {
							latitude: res.latitude,
							longitude: res.longitude
						},
						success: function(res) {
							console.log(res)
							var result = res.result;
							self.city = result.ad_info.city;
							uni.setStorageSync('city', result.ad_info.city);
						}
					});
				}
			})
			}

		},
		onConfirm(e) {
			console.log(e);
			this.keyWords = e.value;
			this.clear();
		},
		clear() {
			this.page = this.totalPage = 1;
			this.status = 'loading';
			this.$refs.homeComList.initData();
			this.loadData();
		},
		navTitleClick(e) {
			console.log(e);
			this.type = e.type;
			this.keyWords = '';
			this.clear();
		},
		getBanner() {
			this._reqw
				.ipost({}, 'banner')
				.then(res => {
					res.result == 0 ? (this.bannerList = res.dataList) : this.$api.tip(res.resultNote);
				})
				.catch(err => {});
		},
		loadData() {
			let n = this.type;
			let parmas = {
				pageNo: this.page,
				pageCount: '10'
			};
			if (this.keyWords != '') {
				parmas.keyWords = this.keyWords;
			} else {
				parmas.type = this.type;
				this.type == 1 ? (parmas.lat = this.point.latitude,parmas.lng = this.point.longitude) : '';
			}
			console.log(parmas);
			this._reqw
				.ipost(parmas, 'worksList')
				.then(res => {
					res.result == 0 ?
						(res.totalPage > 1 ? (this.status = 'more') : (this.status = 'noMore'), (this.totalPage = res.totalPage), this.$refs
							.homeComList.setData(res.dataList)) :
						this.$api.tip(res.resultNote);
				})
				.catch(err => {});
		}
	}
};
